<template>
  <nav class="red lighten-1" style="width: 100%">
    <div class="nav-wrapper">
      <div class="navbar-left">
<!--        <span>{{ date | date('datetime') }}</span>-->
      </div>
      <router-link to="/" class="brand-logo center">Wallet</router-link>
      <ul class="right hide-on-small-and-down">
        <li>
          <a
            class="dropdown-trigger"
            href="#"
            data-target="dropdown"
            ref="dropdown"
          >
            {{ name }}
            <i class="material-icons right">arrow_drop_down</i>
          </a>

          <ul id='dropdown' class='dropdown-content'>
            <li class="divider" tabindex="-1"></li>
            <li>
              <a class="black-text" @click.prevent="logout">
                <i class="material-icons">assignment_return</i>Exit
              </a>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
export default {
  name: "Navbar",
  data: () => ({
    date: new Date(),
    interval: null,
    dropdown: null
  }),
  methods: {
    async logout() {
      await this.$store.dispatch('logout' )
      this.$router.push('/login?message=logout')
    },
  },
  computed: {
    name() {
      return this.$store.getters.info.name
    }
  },
  mounted() {
    this.interval = setInterval(() => {
      this.date = new Date()
    }, 1000)
    // eslint-disable-next-line no-undef
    this.dropdown = M.Dropdown.init(this.$refs.dropdown, {
      constraintWidth: false,
    });
  },
  beforeDestroy() {
    clearInterval(this.interval)
    if (this.dropdown && this.dropdown.destroy) {
      this.dropdown.destroy()
    }
  }
};
</script>

<style scoped>
.navbar {
  margin-top: auto;
}


</style>
